import Vue from 'vue'
import App from './App'
import vuetify from '@/plugins/vuetify'
import i18n from '@/plugins/i18n'
import FlagIcon from 'vue-flag-icon'

Vue.config.productionTip = false
Vue.use(FlagIcon)

new Vue({
  el: '#app',
  vuetify,
  i18n,
  components: { App },
  template: '<App/>'
})



