    font-size: 15px;
  }

  .tm {
    margin-top: 20px;
  }

  .tm-s {
    margin-top: 10px;
  }

  .indented {
    text-indent:14.2pt;
  }

  .double-indented {
    text-indent:28.4pt;
  }

  .triple-indented {
    text-indent:42.6pt;
  }

  #lang_buttons_container {
    display: flex;
    flex-direction: column;
    justify-content: flex-start;
    align-items: flex-start;
  }

  .lang_buttons {
    padding: 12px;
    font-size: 16px;
    margin: 5px;
  }

  .lang_buttons:hover {
    box-shadow: 0 10px 20px rgba(0,0,0,0.19), 0 6px 6px rgba(0,0,0,0.23);
    border-radius: 5px;
  }

  .centered {
    text-align: center;
  }


  .code {
    font-family: monospace;
    margin-top: 8px;
    margin-bottom: 8px;
  }

  table {
    border-collapse: collapse;
  }

  table, th, td {
    border: 1px solid black;
  }

  td {
    padding: 3px 30px;
  }

  .table {
    display: flex;
    flex-direction: column;
    align-items: center;
    justify-content: center;
  }
</style>



